import React, { useState, useEffect, useCallback, FC } from 'react';
import { useHistory, Link } from 'react-router-dom';
import { useSelector, useDispatch } from '../services/hooks';
import {
  setValueAction
} from '../services/actions/form-register';
import { registerUser } from '../services/actions/user';

import { Button, Input } from '@ya.praktikum/react-developer-burger-ui-components';
import Loader from '../components/loader/loader';

import styles from './page-layout.module.css';

import { regExpEmail } from '../utils/regexp';


export const RegisterPage: FC<{}> = () => {
  const dispatch = useDispatch();
  const history = useHistory();
  const {
    form
  } = useSelector((store) => store.formRegister);
  const {
    REGISTER_REQUEST,
    REGISTER_SUCCESS
  } = useSelector((store) => store.user);

  const [isEmailValid, setIsEmailValid] = useState<boolean>(true);
  const [isPasswordValid, setIsPasswordValid] = useState<boolean>(true);
  const [isPasswordVisable, setPasswordVisability] = useState<boolean>(false);

  useEffect(() => {
    if (REGISTER_SUCCESS) {
      history.replace({pathname: '/'});
    }
  }, [history, REGISTER_SUCCESS]);

  const onInputChange = useCallback((
    evt: React.ChangeEvent<HTMLInputElement>
  ) => {
    if (evt.target.name === 'email') {
      setIsEmailValid(regExpEmail.test(evt.target.value));
    }
    if (evt.target.name === 'password') {
      setIsPasswordValid(evt.target.value !== '');
    }

    dispatch(setValueAction({
      name: evt.target.name,
      value: evt.target.value
    }));
  }, [dispatch]);


  const onIconClick = () => {
    setPasswordVisability(!isPasswordVisable);
  };

  const handleSubmit = (evt: React.FormEvent) => {
    evt.preventDefault();
    if ( form.email !== ''
      && isEmailValid
      && form.password !== ''
      && form.name !== '') {
      dispatch(registerUser(form));
    }
  };

  return (
    <section className={`${styles.formContainer} pt-30`}>
      <h1 className={`text text_type_main-medium mb-6`}>
        
      </h1>

      <form onSubmit={handleSubmit}>
        <div className={`${styles.inputWrapper} mb-6`}>
          <Input
            type={'text'}
            placeholder={''}
            onChange={onInputChange}
            value={form.name}
            name={'name'}
            error={false}
            errorText={' '}
            size={'default'}
          />
        </div>

        <div className={`${styles.inputWrapper} mb-6`}>
          <Input
            type={'email'}
            placeholder={'E-mail'}
            onChange={onInputChange}
            value={form.email}
            name={'email'}
            error={!isEmailValid}
            errorText={' email'}
            size={'default'}
          />
        </div>

        <div className={`${styles.inputWrapper} mb-6`}>
          { isPasswordVisable
            ? (
              <Input
                type={'text'}
                placeholder={''}
                onChange={onInputChange}
                icon={'HideIcon'}
                value={form.password}
                name={'password'}
                error={!isPasswordValid}
                onIconClick={onIconClick}
                errorText={' '}
                size={'default'} />
              ) : (
              <Input
                type={'password'}
                placeholder={''}
                onChange={onInputChange}
                icon={'ShowIcon'}
                value={form.password}
                name={'password'}
                error={!isPasswordValid}
                onIconClick={onIconClick}
                errorText={' '}
                size={'default'} />)
          }
        </div>

        <div className={'mb-20'}>
          { REGISTER_REQUEST
            ? (<Loader />)
            : (
              <Button
                type="primary"
                size="medium"
              >
                
              </Button>
            )
          }
        </div>
      </form>

      <p className={'text text_type_main-default text_color_inactive'}>
       ? <Link to='/login' className={styles.link}></Link>
      </p>
    </section>
  );
};
